.profileContainer {
  padding: 50px 0;
}
.profileContent {
  display: flex;
  background: #fff;
  border-radius: 12px;
  overflow: hidden;
  position: relative;
  border: 1px solid #edecec;
}
.profileLeft {
  padding: 50px 30px 20px;
  width: 330px;
  border-right: 1px solid #edecec;
}
.profileRight {
  width: calc(100% - 330px);
}
.avatarImg {
  width: 90px;
  height: 90px;
  position: relative;
  margin-left: auto;
  margin-right: auto;
}
.avatar {
  border-radius: 50%;
}
.changeAvatar {
  position: absolute;
  bottom: 0px;
  right: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  padding: 8px;
  border-radius: 50%;
}
.profileAvatar {
  text-align: center;
  margin-bottom: 20px;
}
.changeAvatarAction {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.fullName {
  text-align: center;
  font-size: 23px;
  line-height: 1;
  font-weight: 500;
  margin-bottom: 5px;
}
.balance {
  text-align: center;
  margin-bottom: 50px;
  font-size: 18px;
  font-weight: 500;
}
.profileTabBtn {
  font-size: 16px;
  font-weight: 500;
  width: 100%;
  padding: 4px 13px;
  line-height: 1;
  margin-bottom: 15px;
  text-align: left;
  border-left: 3px solid #fff;
}
.profileTabBtn.active {
  color: #ff9401;
  border-left: 3px solid #ff9401;
}
.tabCardHeader {
  padding: 19px 27px;
  border-bottom: 1px solid #edecec;
  font-size: 20px;
  font-weight: 600;
}
.tabCardBody {
  padding: 40px 27px;
}
.infoFullName {
  font-size: 23px;
  font-weight: 600;
}
.infoRowFlex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}
.infoAction {
  font-size: 16px;
  font-weight: 400;
  color: #ff9401;
}
.infoAction:hover {
  opacity: 0.8;
}
.infoAction:active {
  opacity: 0.5;
}
.iconEdit {
  margin-left: 10px;
  margin-right: 10px;
}
.infoOuterRow {
  margin-bottom: 18px;
}
.infoData {
  display: flex;
  /* flex-wrap: wrap; */
  /* align-items: center; */
}
.infoTitle {
  font-size: 16px;
  font-weight: 600;
  min-width: 135px;
  line-height: 40px;
}
.infoInput input {
  border: 1px solid #bababa;
  padding: 10px 17px;
  border-radius: 6px;
  min-width: 300px;
}
.changeInfoBtn,
.addressActionBtn {
  font-size: 14px;
  font-weight: 500;
  color: #ff9401;
  text-decoration: underline;
}
.changeInfoBtn:hover,
.addressActionBtn:hover {
  opacity: 0.7;
}
.changeInfoBtn:active,
.addressActionBtn:active {
  opacity: 0.5;
}
.addressRow {
  display: flex;
  margin-bottom: 26px;
}
.addressRowTitle {
  min-width: 120px;
  font-size: 16px;
  font-weight: 600;
}
.addressRowValue > div {
  color: #777;
  font-size: 17px;
}
.addressActions {
  margin-top: 6px;
}
.addressActionBtn {
  text-decoration: none;
}
.addNewAddress button {
  color: #fff;
}
.pOrderRow {
  display: flex;
  margin-bottom: 20px;
}
.pOrderRow:last-of-type {
  margin: 0;
}
.pOrderView {
  width: 120px;
  height: 85px;
  object-fit: cover;
}
.pOrderBody {
  border: 1px solid #edecec;
  border-left: none;
  padding: 10px 18px;
  width: calc(100% - 120px);
  display: flex;
  align-content: space-between;
  flex-wrap: wrap;
}
.pOrderTitle {
  font-weight: 600;
  font-size: 18px;
}
.pOrderPriceDate {
  width: 100%;
  display: flex;
  align-items: center;
  font-weight: 600;
  color: #888;
}
.pOrderCircle {
  width: 8px;
  height: 8px;
  background: #666;
  border-radius: 50%;
  margin: 0 10px;
}
