@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;800;900&display=swap);
/* Global */
* {
  padding: 0;
  margin: 0;
}

body,
html {
  height: 100%;
  scroll-behavior: smooth;
  -ms-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Rubik", sans-serif;
}

.fas,
.fal,
.far,
.fab {
  font-family: "Font Awesome 5 Brands" !important;
}

input,
button {
  outline: none !important;
  border: none;
  background: none;
}

button {
  cursor: pointer;
}

a,
a:hover,
a:focus,
a:active {
  text-decoration: none !important;
  cursor: pointer;
  color: #000;
}

ul {
  margin-bottom: 0;
}

.main-btn {
  transition: all 0.2s ease-in-out;
}

.center-vertically {
  vertical-align: middle;
  overflow-y: auto;
}
.center-vertically:before {
  content: "";
  vertical-align: middle;
  margin-right: -4px;
}
.center-vertically > div {
  vertical-align: middle;
}
.cen-ver-position {
  left: 50%;
  transform: translateX(-50%);
}
.width-fluid {
  width: 100% !important;
}
.bg-title {
  font-size: 36px;
  font-weight: bold;
}
.width-fluid > .height-fluid {
  height: 100%;
}
.form-label {
  font-size: 20px;
  font-weight: 500;
  color: #0f1c49;
  display: block;
}
.form-control {
  display: block;
  color: #000;
  font-size: 16px;
  background: #fffbfa;
  font-weight: 400;
  height: auto;
  border-radius: 8px;
  border: 1px solid #28294b1a;
  width: 100%;
  padding: 10px 15px;
  outline: none;
  resize: none;
  line-height: 1;
}
.form-control:focus {
  box-shadow: none;
}
.form-control::-webkit-input-placeholder {
  color: #28294b60;
  font-size: 15px;
  font-weight: 400;
  line-height: 1;
}
.form-control:-ms-input-placeholder {
  color: #28294b60;
  font-size: 15px;
  font-weight: 400;
  line-height: 1;
}
.form-control::-ms-input-placeholder {
  color: #28294b60;
  font-size: 15px;
  font-weight: 400;
  line-height: 1;
}
.form-control::placeholder {
  color: #28294b60;
  font-size: 15px;
  font-weight: 400;
  line-height: 1;
}
.form-control:hover {
  outline: none;
}
.form-control:active {
  outline: none;
}
.form-control:focus {
  outline: none;
  background: #fffbfa;
  border: 1px solid #28294b1a;
}

input[type="file"] {
  opacity: 0;
  z-index: 20;
  top: 0;
  left: 0;
}

.circle {
  border-radius: 100% !important;
}
.title {
  font-weight: 700;
  font-size: 22px;
}
small {
  color: #333;
  font-weight: 600;
}
.white {
  color: #fff;
}
.black {
  color: #000;
}
.gray {
  color: #ddd;
}
.alert-warning {
  background: none;
  margin-bottom: 15px;
}
.alert-warning .alert-text {
  font-weight: 600;
  font-size: 17px;
}
.alert-warning .alert-text,
.alert-warning .icon {
  color: #cc2631;
}
.alert-warning .icon {
  margin-left: 10px;
  font-size: 28px;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.62);
  height: 100%;
  width: 100%;
}
.icon-right-arrow,
.icon-left-arrow {
  color: #4bc1bc;
  font-weight: 600;
}
.page-header {
  padding: 40px 15px;
  background: #4bc1bc;
}
.page-header .bg-title {
  color: #fff;
  font-size: 43px;
}

/* Form */
.form-data-group {
  margin-bottom: 22px;
}
.form-data-label {
  display: block;
  line-height: 1;
  width: 100%;
  font-weight: 500;
  color: #898989;
  font-size: 14px;
  margin-bottom: 11px;
}
.form-data-input {
  width: 100%;
  display: block;
  border: 2px solid #ccc;
  border-radius: 6px;
  padding: 8px 14px;
  color: #000;
  font-weight: 600;
  position: relative;
  height: 44px;
}
.form-data-input input {
  width: 100%;
}
.form-data-input input::-webkit-input-placeholder {
  color: #aaa;
  font-weight: 400;
}
.form-data-input input:-ms-input-placeholder {
  color: #aaa;
  font-weight: 400;
}
.form-data-input input::-ms-input-placeholder {
  color: #aaa;
  font-weight: 400;
}
.form-data-input input::placeholder {
  color: #aaa;
  font-weight: 400;
}
input[type="password"] {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 100%;
  height: calc(100% - 10px);
  /* font: bold 47px Helvetica, Sans-Serif; */
  padding: 8px 14px;
  z-index: 3;
  -webkit-text-security: circle;
}
input[type="password"]::-webkit-input-placeholder {
  color: #aaa;
  line-height: 50px !important;
  font: 400 14px Arial !important;
}
input[type="password"]:-ms-input-placeholder {
  color: #aaa;
  line-height: 50px !important;
  font: 400 14px Arial !important;
}
input[type="password"]::-ms-input-placeholder {
  color: #aaa;
  line-height: 50px !important;
  font: 400 14px Arial !important;
}
input[type="password"]::placeholder {
  color: #aaa;
  line-height: 50px !important;
  font: 400 14px Arial !important;
}

