/* Form */

/** Buttons **/
.main-btn {
  background: #3498db;
  color: #fff;
  line-height: 1;
  padding: 12px 20px;
  border-radius: 6px;
}
.main-btn:hover {
  color: #fff;
  background: #248ace;
}
.main-btn:active {
  opacity: 0.7;
}
.btn:focus {
  -o-box-shadow: none;
  -ms-box-shadow: none;
  box-shadow: none;
}
.button-pri {
  display: block;
  text-align: center;
  color: #fff;
  background-color: #007bff;
  border: none;
  padding: 12px 23px !important;
  line-height: 1 !important;
  font-size: 15px;
  font-weight: 600;
  margin-bottom: 1px;
  border-radius: 4px;
}
.button-pri:hover {
  background: #217bdb;
}
.button-pri:active {
  opacity: 0.7;
}
.btn:focus {
  border: none !important;
  box-shadow: none !important;
  padding: 10px 23px !important;
  line-height: 1 !important;
  font-size: 1rem !important;
}
.main-button {
  display: inline-block;
  background: #ff9401;
  color: #fff;
  font-weight: 600;

  font-size: 15px;

  padding: 12px 27px;
  line-height: 1;
  border-radius: 50px;
  letter-spacing: 0px;
  /* box-shadow: 4px 4px 6px rgba(255, 148, 1, 0.7); */
}
.main-button:hover {
  background: #f18d03;
  color: #fff !important;
  box-shadow: none;
}
.main-button:active {
  opacity: 0.6;
  color: #fff !important;
}
.main-button:focus {
  color: #fff !important;
}

/* Dropdown */
button.dropdown-toggle {
  padding: 0;
  background: none;
  border: none;
}
button.dropdown-toggle:after {
  content: none;
}
button.dropdown-toggle:focus {
  padding: 0 !important;
}
div.dropdown-menu {
  box-shadow: 0 2px 3px 0 rgba(34, 36, 38, 0.15);
  border: 1px solid rgba(34, 36, 38, 0.15);
  border-radius: 0.28571429rem;
  padding: 5px 0;
}
a.dropdown-item {
  padding: 7px 15px;
  font-size: 14px;
  font-weight: 500;
  color: #5e6061;
}
a.dropdown-item:hover {
  background: rgba(0, 0, 0, 0.05);
}
/* Dropdown */
.wrapper-container {
  padding-top: 95.5px;
}

/* Modal */
div .modal-content {
  border-radius: 12px;
  border: none;
  box-shadow: 0 1px 3px rgb(0 0 0 / 10%), 0 1px 2px rgb(0 0 0 / 10%);
}
div .modal-header,
div .modal-body {
  padding: 15px 25px;
}
div .modal-header {
  /* border: none; */
  padding-bottom: 10px;
}
div .close {
  font-weight: 400;
  font-size: 30px;
}
div .modal-footer {
  padding: 15px 25px;
}
/* Modal */

@media (min-width: 992px) {
  .modal-lg,
  .modal-xl {
    max-width: 600px !important;
  }
}

