.dropdownLink,
.navDropdownList {
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.nav {
  padding: 22px 0;
  box-shadow: 0px 2px 2px #00000017;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #fff;
  z-index: 100;
}
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navLinks a {
  color: #888;
  line-height: 1;
  display: inline-block;
  font-size: 17px;
  font-weight: 400;
}
.navLinks a:hover,
.navLink:global(.active) {
  color: #000;
}
.navLinks {
  display: flex;
}
.navDropdown {
  position: relative;
  margin-left: 26px;
  line-height: 1;
  display: flex;
  align-items: center;
}
.navDropdown:hover .navDropdownList {
  display: block;
}
