.navDropdownList {
  position: absolute;
  top: 53px;
  left: 50%;
  transform: translateX(-50%);
  width: auto;
  background: #fff;
  box-shadow: 0px 2px 2px #00000017;
  min-width: 170px;
  display: none;
}
.dropdownLink {
  display: block;
  text-transform: capitalize;
  padding: 12px;
  line-height: 1;
}
.dropdownLink:hover {
  background: #eee;
  color: #000;
}
.navLink {
  display: flex;
  align-items: center;
}
.arrowDown {
  margin-left: 6px;
}
.navIcon span {
  font-weight: bold;
  font-size: 21px;
}
.navLogoView {
  width: 280px;
}
a.navAuth {
  background: #fdb71417;
  color: #fdb714;
  border: 2px solid #fdb714;
  border-radius: 50px;
  padding: 7px 21px;
  font-size: 15px;
  line-height: 1;
  display: block;
}
a.navAuth:hover {
  background: #fdb714;
  color: #fff;
}
a.navAuth:active {
  opacity: 0.7;
}
.searchContainerNav {
  width: 100%;
  background: #fff;
  position: absolute;
  top: 100%;
  display: flex;
  justify-content: center;
  padding: 25px 0;
  border-top: 1px solid #99999920;
  z-index: 80;
}
span.iconCart {
  font-size: 26px;
  font-weight: normal;
  color: #000;
}
.navCart {
  position: relative;
}
.cartCounter {
  width: 21px;
  height: 21px;
  background: #ff9401;
  border-radius: 50%;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: -6px;
  right: -11px;
  line-height: 1;
  font-size: 13px;
  font-weight: 500;
}
.cartCounter > div {
  margin-top: -2px;
}
.iconGlobe {
  font-size: 25px;
}
.toggleLang {
  position: relative;
}
.langText {
  position: absolute;
  top: -6px;
  right: -9px;
  padding: 4px 5px;
  background: #fff;
  font-size: 11px;
  font-weight: 600;
  box-shadow: 0 1px 3px rgb(0 0 0 / 10%), 0 1px 2px rgb(0 0 0 / 10%);
  border-radius: 4px;
}
.avatar {
  width: 42px !important;
  border-radius: 50%;
}
a.dropdownLink {
  padding: 7px 15px;
  font-size: 14px;
  font-weight: 500;
  color: #5e6061;
  line-height: 1;
  display: inline-block;
}
